import { css } from "emotion";
import { useState } from 'react';
import { Wrapper } from "reactism/elements";
import LightBox from "./lightBox";
import { LoadingGallery } from 'reactism/components';

const ItemOne = ({ dataImage = [] }) => {
  const baseStyle = css`
    .lightbox {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0,0,0,0.8);
      z-index: 99;
      visibility: collapse;
      opacity: 0;
      display: grid;
      img {
        transition: .4s;
        z-index: 999;
        width: 20%;
        margin: auto;
        opacity: 0;
        /* transition: .2s; */
      }
      &.show {
        visibility: visible;
        opacity: 1;
        img {
          opacity: 1;
          width: 50%;
          border-radius: 5px;
        }
      }
    }
    .wrapper {
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      padding-bottom: 2em;
      display: grid;
      grid-template-columns: 1fr;
      > div:nth-child(1) {
        width: 60%;
        h2 {
          margin-bottom: 0;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        p {
          margin-top: 5px;
        }
      }
      .imgGallery {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 1.5em;
        grid-template-areas:
          "col1 col1 col2 col4"
          "col1 col1 col3 col5";
        div {
          cursor: zoom-in;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
          box-shadow: 0px 4px 0px 0 rgba(0, 0, 0, 0.2);
          border-radius: 8px;
        }
        div:nth-child(1) {
          height: 55vh;
          grid-area: col1;
        }
        div:nth-child(2) {
          grid-area: col2;
        }
        div:nth-child(3) {
          grid-area: col3;
        }
        div:nth-child(4) {
          grid-area: col4;
        }
        div:nth-child(5) {
          grid-area: col5;
        }
        div:nth-child(2),
        div:nth-child(3),
        div:nth-child(4),
        div:nth-child(5) {
          height: 25.7vh;
        }
      }
    }

    @media (max-width: 700px) {
      .wrapper {
        .imgGallery {
          div:nth-child(1) {
            height: 30vh;
          }
          grid-template-columns: repeat(2, 1fr);
          grid-template-areas:
            "col1 col1"
            "col2 col3"
            "col4 col5";
        }
      }
    }
  `;

  const [ onVisible, setOnVisible ] = useState(false);

  const handleClick = () => {
    // let lightBox = document.getElementById('lightbox');
    // lightBox.classList.add('show');
    setOnVisible(!onVisible);
  };

  return (
    <div className={baseStyle}>
      {/* <div className="lightbox" id="lightbox">
        <img src="/static/hammer.jpg" alt="" />
      </div> */}
      <LightBox onVisible={ onVisible } setOnVisible={ setOnVisible } />
      <Wrapper pl={7} pr={7}>
      {
        dataImage.length > 0 
        ?
          <div className="wrapper">
            <div>
              <h2>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna</h2>
              <p>20 Oktober 2018</p>
            </div>
            <div className="imgGallery">
                {dataImage.map((itemImg, i) => (
                  <div key={i} onClick={ handleClick }>
                    <img id="imgUrl" src={itemImg.image_url} alt="" />
                  </div>
                ))}
            </div>
          </div>
        :
        <LoadingGallery />
      }
      </Wrapper>
    </div>
  );
};

export default ItemOne;
